import React, { useEffect, useRef, useState } from 'react';
import styled from 'styled-components';
import '../css/tailwind.css';
import mainVideo from '../resource/video/mainVideo.mp4';
import Nav from './Nav';
import LoadingOverlay from 'react-loading-overlay';
import Typing from 'react-typing-animation';
import dotMap from '../css/image/dotmap.png';
import Cursor from 'react-typing-animation/dist/Cursor';
import Portfolio from './Portfolio';
import 'animate.css';

const Main = () => {

    const ticking = useRef(false);
    const lastScrollY = useRef(0);
    const [isLoading, setIsLoading] = useState(true);
    const div1Ref = useRef();
    const moreBtnRef = useRef();
    const mainH1Ref = useRef();


    const [navVisible, setNavVisible] = useState(false);

    useEffect(() => {
        window.addEventListener('scroll', handleScroll, true);
        setIsLoading(false);
        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, [])

    const handleScroll = () => {
        lastScrollY.current = window.scrollY;
        if (!ticking.current) {
            window.requestAnimationFrame(() => {

                console.log(`from Main ${lastScrollY.current}`);

                if (lastScrollY.current > 100) {
                    setNavVisible(true);
                    ticking.current = false;
                }
                else {
                    setNavVisible(false);
                    ticking.current = false;
                }
            });
            ticking.current = true;
        }
    };

    const scrollIt = (element) => {
        window.scrollTo({
            'behavior': 'smooth',
            'left': 0,
            'top': element.offsetTop - 80
        });
    }

    const btnClick = () => {
        scrollIt(div1Ref.current);
        moreBtnRef.current.blur();
    }

    const onFinishedTyping = () => {
        console.dir(mainH1Ref.current);
       // mainH1Ref.current.classList.add('animated 1 flash');
        moreBtnRef.current.classList.remove('hidden');
    }

    return (
        <>
            <Nav navVisible={navVisible}> </Nav>
            <LoadingOverlay
                active={isLoading}
                spinner
                text='Loading'
                fadeSpeed={1500}>

                <HEADER>
                    <H1Div>
                        <Typing cursor={<CustomCursor className=""></CustomCursor>} speed={20} onFinishedTyping={onFinishedTyping}>
                            <Typing.Delay ms={2000} />
                            <H2>고객을 최우선으로 어쩌고 저쩌고~~</H2>
                            <H2>기업 소개 문구 들어갈 자리</H2>
                            <Typing.Reset count={0} delay={300} />
                            <Typing.Speed ms={100} />
                            <Typing.Delay ms={1000} />
                            <button ref={moreBtnRef} onClick={btnClick}><H1 ref={mainH1Ref}>WORLD BEST PARTNERS</H1></button>
                        </Typing>
                        <button ref={moreBtnRef} onClick={btnClick} className="animated 1 zoomIn delay-2s h-16 w-32 mt-6 text-2xl hidden bg-white hover:bg-gray-100 text-gray-800 py-2 px-4 border border-gray-400 rounded shadow">
                            둘러보기
                        </button>
                    </H1Div>

                    <MainVideo loop autoPlay muted>
                        <source src={mainVideo} type="video/mp4" />
                    </MainVideo>
                </HEADER>

            </LoadingOverlay>

            <Portfolio divRef={div1Ref}></Portfolio>

            <DIV2>
                <h1>지도 들어갈 자리</h1>
            </DIV2>
        </>
    )
}

const CustomCursor = styled(Cursor)`
    color : white !important;
`

const HEADER = styled.div`
            display:flex;
            position:relative;
            justify-content:center;
            height: 100vh;
            box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
        `;

const DIV2 = styled.div`
            display:flex;
            position:relative;
            justify-content:center;
            height: 30rem;
            margin-top:2rem;
            border-top-left-radius: 0.5rem;
            border-top-right-radius: 0.5rem;
            color : white;
            background-image: url(${dotMap});
            background-size: 50rem;
            background-repeat: no-repeat;
            background-position:center;
            background-color: #1d1b1b;
        `;

const H1 = styled.h1`
            color: #f7fafc;
            font-size: 2rem;
            z-index:2;
    @media (min-width: 640px) {
                font-size: 5rem;
          }
        `;
const H2 = styled.h1`
            color: #f7fafc;
            font-size: 1.5rem;
            z-index:2;
    @media (min-width: 640px) {
                font-size: 3rem;
          }        
        `;

const MainVideo = styled.video`
            position:absolute;
            width:100%;
            height:100%;
            object-fit:cover;    
        `;
const H1Div = styled.div`
            position:absolute;
            width:100%;
            z-index:2;
            height:100%;
            display:flex;
            flex-direction:column;
            align-items:center;
            justify-content:center;
        `;


export default Main;